<template>
  <div class="root">
    <NavbarBoostrap textTitle="Sekolah Bebas"></NavbarBoostrap>
    <!-- Tampilkan pesan kesalahan jika ada -->
    <div
      v-if="error"
      class="container alert alert-danger alert-dismissible fade show"
      role="alert"
    >
      {{ error }}.
      <button
        type="button"
        class="btn-close"
        data-bs-dismiss="alert"
        aria-label="Close"
        @click="goHome"
      ></button>
    </div>
    <img class="img" alt="Vue logo" src="../../assets/logo.png" />
    <HelloWorld msg="Selamat Datang di Sekolah Bebas Schooba" />
  </div>
</template>

<script>
import HelloWorld from "../components/HelloWorld.vue";
import NavbarBoostrap from "../components/NavbarBoostrap.vue";

export default {
  name: "App",
  components: {
    HelloWorld,
    NavbarBoostrap,
  },
  data() {
    return {
      error: null,
    };
  },
  mounted() {
    this.error = this.$route.params.error || null;

    const kelas = this.$route.query.kelas;
    const bab = this.$route.query.bab;
    const subbab = this.$route.query.subbab;

    // Sekarang Anda memiliki nilai-nilai parameter dari URL
    if (subbab) {
      this.$router.push({ path: `/${kelas}/${bab}/${subbab}` });
    } else if (bab) {
      this.$router.push({ path: `/${kelas}/${bab}` });
    } else if (kelas) {
      this.$router.push({ path: `/${kelas}` });
    }
  },
  methods: {
    goHome() {
      this.$router.push("/");
    },
  },
};
</script>

<style scoped>
.root {
  font-family: Avenir, Helvetica, Arial, sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  text-align: center;
  color: #2c3e50;
  /* margin-top: 60px; */
}
.error-message {
  color: red;
  font-weight: bold;
  /* Gaya pesan kesalahan */
}
.img {
  width: 19rem;
}
</style>
