<template>
  <div>
    <NavBar class="mb-3" :textTittle="bab.name"></NavBar>
    <!-- error -->
    <div
      v-if="error"
      class="container alert alert-danger alert-dismissible fade show mt-3"
      role="alert"
    >
      {{ error }}.
      <button
        type="button"
        class="btn-close"
        data-bs-dismiss="alert"
        aria-label="Close"
        @click="restartError"
      ></button>
    </div>
    <div class="container mt-5 mb-5">
      <BoxTitleDesc :description="bab.about" :tittle="bab.name" />
      <div class="container">
        <BoxListMaterial
          class="mt-2"
          v-for="(subbab, index) in bab.subBab"
          :name="subbab.name"
          :tittle="`lesson ${index + 1} : ${subbab.code}`"
          :key="subbab.code"
          :link="`/${this.$route.params.kelascode}/${
            this.$route.params.babcode
          }/${subbab.code.toLowerCase()}`"
        />
      </div>
    </div>
  </div>
</template>

<script>
import NavBar from '../components/header/NavbarInBab.vue';
import BoxTitleDesc from '../components/BoxTitleDesc.vue';
import BoxListMaterial from '../components/BoxListMaterial.vue';
import {getDataKelas, getBabFromKelas} from '../../controller/babcontroller';
import {mapState, mapMutations} from 'vuex';
import store from '../../store';
import {
  kelasSudahTerisi,
  isiKelas,
  babSudahTerisi,
  isiBab,
} from '../../utils/cheeckingStore';
import {NullData} from '@/models/error/NullData';
import {ErrorNotFound} from '@/models/error/ErrorNotFound';

export default {
  name: 'BabPages',
  components: {
    NavBar,
    BoxTitleDesc,
    BoxListMaterial,
  },
  computed: {
    ...mapState(['kelas', 'bab', 'error']),
  },
  methods: {
    ...mapMutations(['chooseKelas', 'chooseBab']),
    restartError() {
      store.commit('editError', null);
    },
  },
  beforeRouteEnter(to, from, next) {
    const kelascode = to.params.kelascode;
    const babCode = to.params.babcode;

    // cek apakah kelasnya sudah diisi dan kelasnya sesuai yang dipilih
    if (kelasSudahTerisi() && isiKelas(kelascode)) {
      next();
    } else {
      // isi
      getDataKelas(kelascode.toUpperCase())
        .then((kelas) => {
          // menggunakan store karena method belum dibuat untuk mengisi kelas yang dipilih
          store.commit('chooseKelas', kelas);

          // cek apakah babnya sudah diisi dan babnya sesuai yang dipilih
          if (babSudahTerisi() && isiBab(babCode)) {
            next();
          } else {
            // jika belum atau tidak sesuai
            getBabFromKelas(kelas, babCode.toUpperCase())
              .then((bab) => {
                store.commit('chooseBab', bab);
                next();
              })
              .catch((error) => {
                console.log(error);
                let err;
                if (error instanceof SyntaxError) {
                  err = error.message;
                } else if (
                  error instanceof NullData ||
                  error instanceof ErrorNotFound
                ) {
                  err = error.message;
                } else if (error instanceof ReferenceError) {
                  err = `apps is crash becaused reference error : ${error.message}`;
                } else {
                  err = `apps is crash becaused : ${error.message}`;
                }
                store.commit('editError', err);
                next({
                  name: 'kelas',
                  params: {kelascode: kelascode},
                });
              });
          }
        })
        .catch((error) => {
          console.log(error);
          let err;
          if (error instanceof SyntaxError) {
            err = error.message;
          } else if (
            error instanceof NullData ||
            error instanceof ErrorNotFound
          ) {
            err = error.message;
          } else if (error instanceof ReferenceError) {
            err = `apps is crash becaused reference error : ${error.message}`;
          } else {
            err = `apps is crash becaused : ${error.message}`;
          }
          next({name: 'home', params: {error: err}}); // Anda dapat meneruskan error ke rute selanjutnya jika perlu
        });
    }
  },
  mounted() {
    // console.log(this.kelas.about);
  },
};
</script>
