import { Babing } from "./Babing";

/**
 * Class yang mewakili kelas atau mata pelajaran
 * @class
 */
export class Kelas extends Babing {
  /**
   * Instance dari bab
   * @param {string} name
   * @param {string} code
   * @param {string} about
   * @param {Array} bab
   */
  constructor(name, code, about, bab) {
    super(name, code, about, bab);
    this.bab = bab || [];
  }
}
