<template>
  <div>
    <button
      class="btn"
      type="button"
      data-bs-toggle="offcanvas"
      data-bs-target="#offcanvasRight"
      aria-controls="offcanvasRight"
    >
      <i class="bi bi-menu-button-wide" style="font-size: 1.8rem"></i>
    </button>

    <div
      class="offcanvas offcanvas-end"
      tabindex="-1"
      id="offcanvasRight"
      aria-labelledby="offcanvasRightLabel"
    >
      <div class="offcanvas-header">
        <h5 class="offcanvas-title" id="offcanvasRightLabel">
          {{ material.name }}
        </h5>
        <button
          type="button"
          class="btn-close"
          data-bs-dismiss="offcanvas"
          aria-label="Close"
        ></button>
      </div>
      <div class="offcanvas-body">
        <ul
          class="navbar-nav me-auto mb-2 mb-lg-0 d-flex justify-context-center"
        >
          <li
            class="nav-item"
            v-for="(materi, index) in material.materi"
            :key="materi.code"
          >
            <span
              class="btn"
              @click="updateMateri(materi.code)"
              data-bs-dismiss="offcanvas"
              aria-label="Close"
            >
              <i class="bi bi-circle-fill" v-if="index <= indexNow"></i>
              <i class="bi bi-circle" v-else></i> {{ materi.header }}
            </span>
          </li>
        </ul>
      </div>
    </div>
  </div>
</template>

<script>
import {mapActions, mapState} from 'vuex';

export default {
  data() {
    return {
      indexNow: null,
    };
  },
  computed: {
    ...mapState(['material', 'choosenMateri']),
  },
  methods: {
    ...mapActions(['updateChoosenMateri']),
    cekIndexMateriSaat() {
      this.material.materi.forEach((element, index) => {
        if (this.choosenMateri.materi == element.code) {
          this.indexNow = index;
        }
      });
    },
    updateMateri(code) {
      this.updateChoosenMateri(code);
    },
  },
  mounted() {
    this.cekIndexMateriSaat();
  },
  watch: {
    choosenMateri: ['cekIndexMateriSaat'],
  },
};
</script>
