/**
 * Class yang menjadi penanda materi yang sedang dibaca saat ini
 * @class
 */
export class ChoosenMateri {
  /**
   *
   * @param {string} kelas
   * @param {string} Bab
   * @param {string} subbab
   * @param {string} materi
   */
  constructor(kelas, bab, subbab, materi) {
    this.kelas = kelas;
    this.bab = bab;
    this.subbab = subbab;
    this.materi = materi;
  }
}
