import { createRouter, createWebHistory } from "vue-router";
import Home from "./view/page/HomePages.vue";
import KelasPages from "./view/page/KelasPages.vue";
import MaterialPages from "./view/page/MaterialPages.vue";
import BabPages from "./view/page/BabPages.vue";
// import PlayGround from './view/components/PlayGround.vue';

const routes = [
  {
    path: "/",
    component: Home,
  },
  {
    path: "/home/:error",
    name: "home",
    component: Home,
  },
  {
    path: "/:kelascode/:babcode", // Gunakan titik dua untuk menandai parameter
    name: "bab",
    component: BabPages,
  },
  {
    path: "/:kelascode",
    name: "kelas",
    component: KelasPages,
  },
  {
    path: "/:kelascode/:babcode/:subbab",
    name: "material",
    component: MaterialPages,
  },
  // {path: '/bab/', component: PlayGround},
];

const router = createRouter({
  history: createWebHistory(),
  routes,
});

export default router;
