import {createStore} from 'vuex';
import {Bab} from './models/Bab';
import {Kelas} from './models/Kelas';
import {SubBab} from './models/SubBab';
import {Material} from './models/Material';
import {ChoosenMateri} from './models/ChoosenMateri';

const store = createStore({
  state: {
    // Definisikan state Anda di sini
    kelas: new Kelas(),
    bab: new Bab(),
    subbab: new SubBab(),
    material: new Material(),
    choosenMateri: new ChoosenMateri(),
    error: null,
  },
  mutations: {
    // Mutasi untuk menambahkan kelas baru ke state
    chooseKelas(state, newKelas) {
      state.kelas = newKelas;
    },
    // Mutasi lainnya jika diperlukan
    chooseBab(state, newBab) {
      state.bab = newBab;
    },
    chooseSubBab(state, newSubBab) {
      state.subbab = newSubBab;
    },
    chooseSubBabMaterial(state, newSubBabMaterial) {
      state.material = newSubBabMaterial;
    },
    createChoosenMateri(state, choosenMateri) {
      state.choosenMateri = choosenMateri;
    },
    editError(state, val) {
      state.error = val;
    },
  },
  actions: {
    // Tindakan untuk menambahkan bab baru dengan menyebarkan mutasi
    chooseNewKelas({commit}, newKelas) {
      commit('chooseKelas', newKelas);
    },
    // Tindakan lainnya jika diperlukan
    chooseNewBab({commit}, newBab) {
      commit('chooseBab', newBab);
    },
    chooseNewSubBab({commit}, newSubBab) {
      commit('chooseSubBab', newSubBab);
    },
    chooseNewSubBabMaterial({commit}, newSubBabMaterial) {
      commit('chooseSubBabMaterial', newSubBabMaterial);
    },
    updateChoosenMateri({commit}, codeMateri) {
      const choosen = new ChoosenMateri(
        this.state.kelas.code,
        this.state.bab.code,
        this.state.subbab.code,
        codeMateri
      );
      commit('createChoosenMateri', choosen);
    },
  },
  getters: {
    // Getter untuk mendapatkan semua babs dari state
    getKelas: (state) => state.kelas,
    // Getter lainnya jika diperlukan
    getBab: (state) => state.bab,
    getSubBab: (state) => state.Subbab,
    getSubBabMaterial: (state) => state.SubbabMaterial,
  },
});
export default store;
