import axios from "axios";
import { Kelas } from "../models/Kelas";
import { Bab } from "../models/Bab";
import { SubBab } from "../models/SubBab";
// import { NetworkError } from "@/models/error/NetworkError";
import { ErrorNotFound } from "@/models/error/ErrorNotFound";
import { NullData } from "@/models/error/NullData";
import { Material } from "@/models/Material";
import { Materi } from "@/models/Materi";

export function getDataKelas(kelascode) {
  return axios
    .get("/basisdata/kelas.json") // Sesuaikan dengan path file JSON Anda
    .then((response) => {
      // if (!response.ok) {
      //   throw new NetworkError("Network response was not ok");
      // }
      const jsonData = response.data; // Simpan data ke variabel lokal
      if (jsonData.length === 0) {
        throw new NullData("Data is Empty");
      }
      const kelas = new Kelas();
      jsonData.forEach(function (data) {
        if (data["kelas code"] === kelascode) {
          kelas.name = data.nama;
          kelas.about = data.keterangan;
          kelas.code = kelascode;
          kelas.bab = data.bab;
        }
      });
      if (!kelas.name) {
        throw new ErrorNotFound("The Class is not found");
      }

      const bab = [];
      kelas.bab.forEach(function (data) {
        if (data.bab) {
          const databab = new Bab(
            data.bab,
            data["bab code"],
            data.keterangan,
            data["data file"],
            data.subbab
          );
          bab.push(databab);
          const subbab = [];
          databab.subBab.forEach((element) => {
            if (element.title) {
              const datasubbab = new SubBab(
                element.title,
                element["subbab code"]
              );
              subbab.push(datasubbab);
            }
          });
          databab.subBab = subbab;
        }
      });
      kelas.bab = bab;

      return kelas;
    })
    .catch((error) => {
      if (error instanceof Error) {
        throw new Error(`Error: ${error.message}`);
      } else {
        if (error.stack) {
          throw new Error(error.stack);
        } else {
          throw new Error("Unknown error");
        }
      }
    });
}

export function getBabFromKelas(kelas, babcode) {
  return new Promise((resolve, reject) => {
    let found = false;
    const bab = new Bab();
    kelas.bab.forEach(function (data) {
      if (data.code == babcode) {
        bab.name = data.name;
        bab.about = data.about;
        bab.code = babcode;
        bab.file = data.file;
        bab.subBab = data.subBab;
        // panggil resolve dengan hasilnya
        resolve(bab);
        found = true;
      }
    });
    if (!found) {
      // panggil reject dengan pesan kesalahan
      reject(new ErrorNotFound("Data bab is Not Found"));
    }
  });
}

export function getSubBabFromBab(bab, subbabcode) {
  return new Promise((resolve, reject) => {
    let found = false;
    bab.subBab.forEach(function (params) {
      if (params.code == subbabcode) {
        console.log(params.code);
        console.log(subbabcode);
        const subbab = new SubBab();
        subbab.name = params.name;
        subbab.code = subbabcode;
        resolve(subbab);
        found = true;
      }
    });
    if (!found) {
      // panggil reject dengan pesan kesalahan
      reject(new ErrorNotFound("Data bab is Not Found"));
    }
  });
}

export function getDataMaterial(file, kodesubbab) {
  return axios
    .get(`/basisdata/${file}`)
    .then((hasil) => {
      const jsonData = hasil.data;
      console.log(jsonData);
      if (jsonData.length === 0) {
        throw new NullData("Data is Empty");
      }
      const material = new Material();
      jsonData.forEach(function (data) {
        if (data["subbab code"] == kodesubbab) {
          material.name = data.judul;
          material.subbabcode = data["subbab code"];
          material.materi = data.materi;
        }
      });

      if (!material.name) {
        throw new ErrorNotFound("The Class is not found");
      }

      const newMaterial = [];
      material.materi.forEach(function (data) {
        const material = new Materi(
          data.code,
          data.header,
          data.body,
          data.style,
          data.tipe
        );
        newMaterial.push(material);
      });
      material.materi = newMaterial;
      return material;
    })
    .catch((error) => {
      if (error instanceof Error) {
        throw new Error(`Error: ${error.message}`);
      } else {
        if (error.stack) {
          throw new Error(error.stack);
        } else {
          throw new Error("Unknown error");
        }
      }
    });
}
