/**
 * Class yang mewakili materi yang akan di tampilkan
 * @class
 */
export class Materi {
  /**
   *
   * @param {string} code
   * @param {string} header
   * @param {string} body
   * @param {string} style
   * @param {string} type
   */
  constructor(code, header, body, style, type) {
    this.code = code;
    this.header = header;
    this.body = body;
    this.style = style;
    this.type = type;
  }
}
