import { Babing } from "./Babing";

/**
 * Class yang mewakili data material di dalam setiap subbab
 * @class
 */
export class Material extends Babing {
  /**
   *
   * @param {string} name
   * @param {string} code
   * @param {Array} materi
   */
  constructor(name, code, materi) {
    super(name, code); // Call the parent class constructor
    this.subbabcode = this.code; // Renaming the property
    this.materi = materi;
  }
}
