<template>
  <div>
    <NavBar class="mb-3" :textTittle="kelas.name" />
    <!-- error -->
    <div
      v-if="error"
      class="container alert alert-danger alert-dismissible fade show mt-3"
      role="alert"
    >
      {{ error }}.
      <button
        type="button"
        class="btn-close"
        data-bs-dismiss="alert"
        aria-label="Close"
        @click="restartError"
      ></button>
    </div>
    <div class="container mt-3">
      <BoxTitleDesc :description="kelas.about" :tittle="kelas.name" />
      <div class="container">
        <BoxListMaterial
          class="mt-2"
          v-for="(bab, index) in kelas.bab"
          :name="bab.name"
          :tittle="`bab ${index + 1} : ${bab.code}`"
          :key="bab.code"
          :link="`/${this.$route.params.kelascode}/${bab.code.toLowerCase()}`"
        />
      </div>
    </div>
  </div>
</template>

<script>
import NavBar from '../components/header/NavbarInKelas.vue';
import BoxTitleDesc from '../components/BoxTitleDesc.vue';
import BoxListMaterial from '../components/BoxListMaterial.vue';
import {kelasSudahTerisi, isiKelas} from '../../utils/cheeckingStore';
import {getDataKelas} from '../../controller/babcontroller';
import store from '../../store';
import {NullData} from '@/models/error/NullData';
import {ErrorNotFound} from '@/models/error/ErrorNotFound';
import {mapMutations, mapState} from 'vuex';

export default {
  name: 'KelasPages',
  components: {
    NavBar,
    BoxTitleDesc,
    BoxListMaterial,
  },
  computed: {
    ...mapState(['kelas', 'error']),
  },
  methods: {
    ...mapMutations(['chooseKelas']),
    restartError() {
      store.commit('editError', null);
    },
  },
  beforeRouteEnter(to, from, next) {
    const kelascode = to.params.kelascode;
    // cek apakah kelasnya sudah diisi dan kelasnya sesuai yang dipilih
    if (kelasSudahTerisi() && isiKelas(kelascode)) {
      next();
    } else {
      // isi
      getDataKelas(kelascode.toUpperCase())
        .then((kelas) => {
          // menggunakan store karena method belum dibuat untuk mengisi kelas yang dipilih
          store.commit('chooseKelas', kelas);
          next();
        })
        .catch((error) => {
          let err;
          if (error instanceof SyntaxError) {
            err = error.message;
          } else if (
            error instanceof NullData ||
            error instanceof ErrorNotFound
          ) {
            err = error.message;
          } else if (error instanceof ReferenceError) {
            err = `apps is crash becaused reference error : ${error.message}`;
          } else {
            err = `apps is crash becaused : ${error.message}`;
          }
          next({name: 'home', params: {error: err}});
        });
    }
  },
};
</script>
