<template>
  <div>
    <NavBar />
    <div class="progress-container" id="containerBar">
      <div class="progress-bar" id="myBar"></div>
    </div>
    <!-- error -->
    <div
      v-if="error"
      class="container alert alert-danger alert-dismissible fade show mt-3"
      role="alert"
    >
      {{ error }}.
      <button
        type="button"
        class="btn-close"
        data-bs-dismiss="alert"
        aria-label="Close"
        @click="restartError"
      ></button>
    </div>
    <!-- button menu -->
    <MenuButton id="menuButton" style="display: none" />
    <Materi class="container mb-5 pt-5" />
  </div>
</template>

<script>
import NavBar from "../components/header/NavbarInMaterial.vue";
import Materi from "../components/MateriBuilder.vue";
import MenuButton from "../components/MenuClassButton.vue";
import {
  kelasSudahTerisi,
  isiKelas,
  babSudahTerisi,
  isiBab,
  subbabSudahTerisi,
  isiSubBab,
  materialSudahTerisi,
  isimaterial,
  choosenMaterialSudahTerisi,
} from "../../utils/cheeckingStore";
import { getDataKelas } from "@/controller/babcontroller";
import store from "../../store";
import {
  getBabFromKelas,
  getDataMaterial,
  getSubBabFromBab,
} from "../../controller/babcontroller";
import { mapMutations, mapState } from "vuex";
import { getFirstMateriCode } from "../../utils/gettingDataFromStore";
import { ChoosenMateri } from "../../models/ChoosenMateri";
import { NullData } from "@/models/error/NullData";
import { ErrorNotFound } from "@/models/error/ErrorNotFound";

export default {
  name: "MaterialPages",
  components: {
    NavBar,
    Materi,
    MenuButton,
  },

  async beforeRouteEnter(to, form, next) {
    const kelascode = to.params.kelascode;
    const babCode = to.params.babcode;
    const subbabCode = to.params.subbab;
    // cek apakah kelasnya sudah diisi dan kelasnya sesuai yang dipilih jika belum maka isi
    if (!(kelasSudahTerisi() && isiKelas(kelascode.toUpperCase()))) {
      await getDataKelas(kelascode.toUpperCase())
        .then((kelas) => {
          // lalu isikan kelasnya
          store.commit("chooseKelas", kelas);
        })
        .catch((error) => {
          let err;
          if (error instanceof SyntaxError) {
            err = error.message;
          } else if (
            error instanceof NullData ||
            error instanceof ErrorNotFound
          ) {
            err = error.message;
          } else if (error instanceof ReferenceError) {
            err = `apps is crash becaused reference error : ${error.message}`;
          } else {
            err = `apps is crash becaused : ${error.message}`;
          }
          next({ name: "home", params: { error: err } });
        });
    }
    // cek apakah babnya sudah diisi dan babnya sesuai yang dipilih jika belum maka isi
    if (!(babSudahTerisi() && isiBab(babCode.toUpperCase()))) {
      const kelas = store.state.kelas;
      await getBabFromKelas(kelas, babCode.toUpperCase())
        .then((bab) => {
          store.commit("chooseBab", bab);
        })
        .catch((error) => {
          let err;
          if (error instanceof SyntaxError) {
            err = error.message;
          } else if (
            error instanceof NullData ||
            error instanceof ErrorNotFound
          ) {
            err = error.message;
          } else if (error instanceof ReferenceError) {
            err = `apps is crash becaused reference error : ${error.message}`;
          } else {
            err = `apps is crash becaused : ${error.message}`;
          }
          store.commit("editError", err);
          next({ name: "kelas", params: { kelascode: kelascode } });
          return;
        });
    }
    // cek apakah subbabnya sudaah diisi dan sesuai yang dipilih jika belum maka isi
    if (!(subbabSudahTerisi() && isiSubBab(subbabCode.toUpperCase()))) {
      const bab = store.state.bab;
      await getSubBabFromBab(bab, subbabCode.toUpperCase())
        .then((subbab) => {
          store.commit("chooseSubBab", subbab);
        })
        .catch((error) => {
          let err;
          if (error instanceof SyntaxError) {
            err = error.message;
          } else if (
            error instanceof NullData ||
            error instanceof ErrorNotFound
          ) {
            err = error.message;
          } else if (error instanceof ReferenceError) {
            err = `apps is crash becaused reference error : ${error.message}`;
          } else {
            err = `apps is crash becaused : ${error.message}`;
          }
          store.commit("editError", err);
          next({
            name: "bab",
            params: { kelascode: kelascode, babcode: babCode },
          });
          return;
        });
    }
    // cek apakah materinya sudah diisi dan sesuai apa yang dipilih
    if (!(materialSudahTerisi() && isimaterial(subbabCode.toUpperCase()))) {
      console.log("data materi masih kosong");
      await getDataMaterial(store.state.bab.file, subbabCode.toUpperCase())
        .then((data) => {
          store.commit("chooseSubBabMaterial", data);
        })
        .catch((error) => {
          let err;
          if (error instanceof SyntaxError) {
            err = error.message;
          } else if (
            error instanceof NullData ||
            error instanceof ErrorNotFound
          ) {
            err = error.message;
          } else if (error instanceof ReferenceError) {
            err = `apps is crash becaused reference error : ${error.message}`;
          } else {
            err = `apps is crash becaused : ${error.message}`;
          }
          store.commit("editError", err);
          next({
            name: "bab",
            params: { kelascode: kelascode, babcode: babCode },
          });
          return;
        });
    }
    // cek apakah choosen materi sudah ditetapkan dan telah sesuai dengan bab subbab yang ada saat ini
    if (
      !(
        choosenMaterialSudahTerisi() &&
        store.state.choosenMateri.kelas == kelascode &&
        store.state.choosenMateri.bab == babCode &&
        store.state.choosenMateri.subbab == subbabCode
      )
    ) {
      const choosen = new ChoosenMateri(
        kelascode.toUpperCase(),
        babCode.toUpperCase(),
        subbabCode.toUpperCase(),
        getFirstMateriCode().toUpperCase()
      );
      store.commit("createChoosenMateri", choosen);
    }
    next();
  },
  data() {
    return {
      scrollY: 0,
    };
  },
  computed: {
    ...mapState(["kelas", "bab", "subbab", "material", "error"]),
  },
  mounted() {
    // Menambahkan event listener untuk mengikuti perubahan scroll
    window.addEventListener("scroll", this.handleScroll);
  },
  beforeUnmount() {
    // Pastikan untuk membersihkan event listener ketika komponen di-unmount
    window.removeEventListener("scroll", this.handleScroll);
  },
  methods: {
    ...mapMutations(["chooseKelas"]),
    restartError() {
      store.commit("editError", null);
    },
    // ketika di scroll
    handleScroll() {
      // ambil nilai tinggi scroll
      this.scrollY = window.scrollY;
      // ambil elemen indikator
      const bar = document.getElementById("containerBar");
      const menuButton = document.getElementById("menuButton");
      // jalankan update indikator scroll
      this.startScrollIndicator();
      // jika nilai tinggi scroll > 56px
      if (this.scrollY > 56) {
        bar.classList.add("fix-top");
        menuButton.classList.add("menu-fix");
        menuButton.style.display = "block";
      } else {
        bar.classList.remove("fix-top");
        menuButton.style.display = "none";
      }
    },
    startScrollIndicator() {
      var winScroll =
        document.body.scrollTop || document.documentElement.scrollTop;
      var height =
        document.documentElement.scrollHeight -
        document.documentElement.clientHeight;
      var scrolled = (winScroll / height) * 100;
      document.getElementById("myBar").style.width = scrolled + "%";
      // console.log(winScroll);
    },
  },
};
</script>

<style scoped>
/* The progress container (grey background) */
.progress-container {
  width: 100%;
  height: 8px;
  background: #ccc;
}

/* The progress bar (scroll indicator) */
.progress-bar {
  height: 8px;
  background: #7db1ec;
  width: 0%;
}
.fix-top {
  position: fixed;
  z-index: 10;
  top: 0;
}
.menu-fix {
  position: fixed;
  z-index: 11;
  top: 1rem;
  right: 1rem;
}
</style>
