import { Babing } from "./Babing";

/**
 * Class yang mewakili bab di dalam setiap kelas
 * @class
 */
class Bab extends Babing {
  /**
   *
   * @param {string} name
   * @param {string} code
   * @param {string} about
   * @param {string} file
   * @param {Array} subBab
   */
  constructor(name, code, about, file, subBab) {
    super(name, code, about);
    this.subBab = subBab || [];
    this.file = file;
  }
}

export { Bab };
