<template>
  <div class="card">
    <h5 class="card-header">{{ header }}</h5>
    <div class="card-body" v-html="materi"></div>
  </div>
</template>

<script>
import { mapActions, mapState } from "vuex";

export default {
  data() {
    return {
      indexNow: null,
      materi: "",
      header: "",
    };
  },
  computed: {
    ...mapState(["material", "choosenMateri"]),
  },
  methods: {
    ...mapActions(["updateChoosenMateri"]),
    cekIndexMateriSaat() {
      this.material.materi.forEach((element, index) => {
        if (this.choosenMateri.materi == element.code) {
          this.indexNow = index;
        }
      });
    },
    getMateri() {
      this.material.materi.forEach((val) => {
        if (val.code == this.choosenMateri.materi) {
          // const parser = new DOMParser();
          // this.materi = parser.parseFromString(val.body, 'text/html');
          this.header = val.header;
          this.materi = val.body;
          // console.log(this.materi);
        }
      });
    },
  },
  mounted() {
    this.cekIndexMateriSaat();
    this.getMateri();
  },
  watch: {
    choosenMateri: ["cekIndexMateriSaat", "getMateri"],
  },
};
</script>

<style scoped></style>
