<template>
  <div>
    <Materi />
    <div class="buttoning">
      <button
        type="button"
        class="btn"
        v-if="!(indexNow == 0)"
        @click="updateMateri(getCodeByIndex(indexNow - 1))"
      >
        <i class="bi bi-arrow-bar-left fs-5"></i> Back
      </button>
      <button
        type="button"
        class="btn btn-light disabled"
        style="border-radius: 50%"
      >
        <img
          src="../../assets/logo.png"
          alt="Schooa logo"
          class="button-logo"
        />
      </button>
      <button
        type="button"
        class="btn"
        v-if="!(indexNow + 1 == this.material.materi.length)"
        @click="updateMateri(getCodeByIndex(indexNow + 1))"
      >
        Next <i class="bi bi-arrow-bar-right fs-5"></i>
      </button>
    </div>
  </div>
</template>

<script>
import Materi from "./BoxMaterial.vue";
import { mapActions, mapState } from "vuex";

export default {
  components: {
    Materi,
  },
  data() {
    return {
      indexNow: null,
    };
  },
  computed: {
    ...mapState(["material", "choosenMateri"]),
  },
  methods: {
    ...mapActions(["updateChoosenMateri"]),
    cekIndexMateriSaat() {
      this.material.materi.forEach((element, index) => {
        if (this.choosenMateri.materi == element.code) {
          this.indexNow = index;
        }
      });
    },
    updateMateri(code) {
      this.updateChoosenMateri(code);
    },
    getCodeByIndex(payload) {
      let code;
      this.material.materi.forEach((val, index) => {
        if (payload == index) {
          code = val.code;
        }
      });
      return code;
    },
  },
  mounted() {
    this.cekIndexMateriSaat();
  },
  watch: {
    choosenMateri: "cekIndexMateriSaat",
  },
};
</script>

<style scoped>
.buttoning {
  display: flex !important;
  justify-content: space-between;
  margin-top: 40px;
}
.button-logo {
  width: 3rem;
  height: 3.1rem;
}
@media only screen and (max-width: 221px) {
  .button-logo {
    display: none;
  }
}
@media only screen and (min-width: 350px) {
  .button-logo {
    width: 4rem;
    height: 4.1rem;
  }
}
@media only screen and (min-width: 650px) {
  .button-logo {
    width: 5rem;
    height: 5.1rem;
  }
}
</style>
